<template>
  <div class="row">
    <div class="col-xs-offset-2 col-xs-8">
      <div class="page-header"><h2>Vue Router Demo</h2></div>
      <button @click="back">后退（back）</button>
      <button @click="forward">前进(forward)</button>
      <button @click="go">go几步</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Banner",
  methods: {
    back() {
      this.$router.back();
    },
    forward(){
      this.$router.forward();
    },
    go(){
      this.$router.go(3);
    }
  },
};
</script>

