//该文件专门用于创建整个应用的路由器

import VueRouter from 'vue-router'
import About from '../pages/About'
import Home from '../pages/Home'
import News from '../pages/News'
import Message from '../pages/Message'
import Detail from '../pages/Detail'


//创建并暴露一个路由器
const router = new VueRouter({
    mode:'hash',  //默认hash模式 路径带#
    // mode:'history',
    routes: [
        {
            name: 'guanyu',
            path: '/about',
            component: About,
            meta: {isAuth: true, title: '关于' },

        },
        {
            name: 'zhuye',
            path: '/home',
            component: Home,
            meta: { title: '主页' },
            //通过children配置子级路由
            children: [
                {
                    name: 'xinwen',
                    path: 'news',  //此处不要写斜杠/
                    component: News,
                    meta: { isAuth: true, title: '新闻' },
                    // 独享路由守卫 beforeEnter:()=>{},   只有进入前
                    beforeEnter:(to,from,next)=>{
                        if (to.meta.isAuth) {
                            if (localStorage.getItem('userid') === '415') {
                                next();
                            } else {
                                alert('用户密码不正确');
                            }
                        } else {
                            next();
                        }
                    }

                },
                {
                    name: 'xiaoxi',
                    path: 'message',
                    component: Message,
                    meta: { isAuth: true, title: '消息' },
                    children: [
                        {
                            name: 'xiangqing',
                            path: 'detail/:id/:title',
                            component: Detail,
                            meta: { title: '详情' },

                            // props的第一种写法，值为对象，该对象的所有key-value值都会以prop的形式传给Detail组件
                            // props:{a:1,b:'hello'}

                            // props的第二种写法，值为布尔值，若布尔值为真，就会把路由组件收到的所有params参数以prop的形式传给Detail组件
                            props: true,

                            // props的第三种写法，值为函数
                            /*  props($route){
                                 return{
                                     id:$route.query.id,
                                     title:$route.query.title
                                 }
                             } */

                        }
                    ]
                }
            ]
        },
    ]
})

//全局前置路由守卫——初始化的时候被调用、每次路由切换之前被调用
router.beforeEach((to, from, next) => {
    console.log('全局前置路由守卫',to, from);
    //也可以写成 if(to.name ==='xinwen'||to.name ==='xiaoxi')或者
    // if(to.path ==='/home/news'||to.path ==='/home/message')
    if (to.meta.isAuth) {
        if (localStorage.getItem('username') === 'xiaoyang') {
            next();
        } else {
            alert('用户名不正确');
        }
    } else {
        next();
    }
})

//全局后置路由守卫——初始化的时候被调用、每次路由切换之后被调用
router.afterEach((to,from) => {
    console.log('后置路由守卫', to, from);
    document.title = to.meta.title||'学生系统';
})

export default router