<template>
   <h2>我是About的内容</h2>
</template>

<script>
export default {
    name:'About',
    //通过路由规则进入该组件时被调用beforeRouteEnter(){}
    beforeRouteEnter(to,from,next){
        if(to.meta.isAuth){ //判断是否需要鉴权
            if(localStorage.getItem('userage')==='18'){
                next()
            }else{
                alert('未满18岁');
            }
        }else{
            next();
        }
    },
    //通过路由规则离开该组件时被调用beforeRouteLeave(){}
    beforeRouteLeave(to,from,next){
        console.log('About---beforeRouteLeave被调用了',to,from);
        next();
    }
}
</script>

